import React from 'react';
import {withRouter} from 'react-router-dom';
import Author from '../Author/Author';
import PublishedAt from '../PublishedAt/PublishedAt';
import './NewsItem.scss';

const NewsItem = props => {

    return(
        <div className='newsItem' onClick={() => {
            props.history.push('/news/' + props.newsInfo.title);
        }}>
            <h3 className='newsItemTitle'>{props.newsInfo.title}</h3>
            <div className='newsItemInfo'>
                <Author name={props.newsInfo.author}/>
                <PublishedAt date={props.newsInfo.publishedAt}/>
            </div>
        </div>
    )
}

export default withRouter(NewsItem);