import React from 'react';
import {withRouter} from 'react-router-dom';
import Author from '../Author/Author';
import NewsTitle from '../NewsTitle/NewsTitle';
import PublishedAt from '../PublishedAt/PublishedAt';
import './NewsDetail.scss';

const NewsDetail = props => {
    return(
        <div className='newsDetail'>
            <div className='newDetailCol'>
                <NewsTitle title={props.news.title}/>
                <Author name={props.news.source.name}/>
                <PublishedAt date={props.news.publishedAt}/>
            </div>
            <div className='newDetailCol'>
                <img src={props.news.urlToImage} alt='source img'/>
                <div>
                    {props.news.description}
                </div>
            </div>
            
        </div>
    )
}

export default withRouter(NewsDetail);