import React from 'react';
import NewsItem from '../NewsItem/NewsItem';
import {withRouter} from 'react-router-dom';
import './Main.scss';

const Main = props => {

    const newsList = props.news.map(
        (newsItem, index) => <NewsItem key={index} newsInfo={newsItem}/>)
        .slice(0, 6);

    return(
        <div className="main">
            <div className='mainTitle'>
                Быть<br/> в курсе <span className='mainTitleBlue'>событий</span>
            </div>
            {newsList}
            <button className='mainToNews' onClick={() =>
                props.history.push('/news/')
            }>Быть в курсе событий</button>
        </div>
    )
}

export default withRouter(Main);