import React, { useEffect, useState } from 'react';
import './App.scss';
import {Route} from 'react-router-dom';
import Contacts from './components/Contacts/Contacts';
import Main from './components/Main/Main';
import News from './components/News/News';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import NewsDetail from './components/NewsDetail/NewsDetail';

const App = () => {
  const key = '7898e47a98234d85870beb1fe5669acd';
  const [state, setState] = useState([]);

  const getNews = () => {
    const url = `https://newsapi.org/v2/everything?q=Apple&from=2021-05-26&sortBy=popularity&apiKey=${key}`;
    const req = new Request(url);

    fetch(req)
      .then(response => response.json())
      .then(json => {
        setState([...json.articles]);
      });
  }

  useEffect(() =>{
    getNews();
  }, []);

  return (
    <div className="App">
      <Header/>
      <Route
        path='/'
        exact
        component={() => <Main news={state}/>}
      />
      <Route
        path='/news'
        exact
        component={() => <News news={state}/>}
      />
      <Route
        path='/contacts'
        exact
        component={() => <Contacts/>}
      />
      <Route
        path='/news/:url'
        exact
        render={({match}) => <NewsDetail news={state.find(news => news.title === match.params.url)}/>}
      />
      <Footer/>
    </div>
  );
}

export default App;
