import React from 'react';
import './PublishedAt.scss';

const PublishedAt = props => {
    const beautifyDate = (number) => {
        if (number < 10){
            return '0' + number;
        }
        else {
            return number;
        }
    }
    const date = new Date(props.date);   

    return (
        <div className='date'>
            <span className='day'>{beautifyDate(date.getDay())}</span> / {beautifyDate(date.getMonth())}
        </div>
    )
}


export default PublishedAt;