import React from 'react';
import './NewsTitle.scss';

const NewsTitle = props => {

    let titleArr = props.title.split(' ');
    const blackString = titleArr.slice(0, titleArr.length-1).join(' ');
    const blueString = ' '+titleArr[titleArr.length-1];
    return(
        <h3 className="title">
            {blackString}<span className="titleBlue">{blueString}</span>
        </h3>
    )
}

export default NewsTitle;