import React from 'react';
import './Author.scss';

const Author = props => {
    const beautifyAuthor = (line) => {
        if (line.length > 23) {
            return line.slice(0, 20) + '...';
        }
        return line;
    }
    const author = beautifyAuthor(props.name);

    return(
        <span className='author'>{author}</span>
    )
}

export default Author;